export default {
	data() {
		return {
			login: false,
			avatarUrl: "../../static/images/avatar.png",
			uerInfo: {}
		}
	},
	onLoad() {
		let userinfo = uni.getStorageSync('userinfo');
		if (userinfo) {
			this.login=true;
			this.uerInfo = userinfo;
		}
	},
	methods: {
		goLogin() {
			if (!this.login) {
				uni.navigateTo({
					url: 'login'
				});
			}
		},
		gourl(url){
			if (this.login) {
				uni.navigateTo({
					url: url
				});
			}else{
				uni.navigateTo({
					url: 'login'
				});
			}
		}
	}
}
